
import { _decorator, Component, Node } from 'cc';
import { GameManager } from './GameManager';
import { Player } from './Player';
const { ccclass, property } = _decorator;
 
@ccclass('TouchManager')
export class TouchManager extends Component {
    @property({type: Player})
    public player: Player|null = null;

    @property
    public direction: number = 0;

    start () {
        this.node.on(Node.EventType.TOUCH_START, (event: any) => 
        {
            this.player.getComponent(Player).Move(this.direction);
            //GameManager.Instance.AddScore();

            if (GameManager.Instance.AddScore() >= 10)
            {
                this.player.getComponent(Player).ChangeColor();
            }
            
        }, this);
    }
}
